.App {
  text-align: center;
  background-color: #181c20;
  min-height: 100vh;
}

.navbar {
  background-color: #181c20;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 50px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  justify-content: flex-end;
  z-index: 1;
}

.App-header h1 {
  margin: 0;
}

.App-footer {

  height: 50px;
  justify-content: center;
  color: white;
}

.App-footer h1 {
  margin: 0;
}

.App-link {
  color: #61dafb;
}

.Home-section {

  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 100vh;
  margin-left: 10px;
  margin-right: 10px;
  justify-content: center;
}

.section {
  min-height: 83vh;
  padding-top: 50px;
  margin-left: 20px;
  margin-right: 20px;
}

.row {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.col-l {
  width: 50%;
  height: auto;
}

.col-r {
  width: 50%;
  height: auto;
}

.p_pic {
  height: 240px;
  width: auto;
  border-radius: 50%;
}

.text-p {
  text-align: left;
  color: white;
}
.text-p h1 {
  font-size: 2.5rem;
  margin: 0;
}
.text-p h2 {
  font-size: 2.5rem;
  margin: 0;
  text-align: center;
}

.text-p h5 {
  font-size: 1.7rem;
  margin: 0;

}

.socials {
  padding: 3px;
  /* margin-top: 20px; */
  width: 2rem;
  fill: white;
  margin-right: 7px;
}

.pt-2 {
  padding-top: 20px;
}

.nav-items {
  text-align: right;
  margin-right: 20px;
  display: flex;
  flex-direction: row;
}

.nav-items h4 {
  /* padding-right: 30px; */
  margin: 20px;
}

/* Mobile Screen */

@media screen and (max-width: 500px) {
  .col-l {
    width: 100%;
  }

  .col-r {
    width: 100%;
    padding-top: 20px;
  }

  .text-p {
    padding-left: 20px;
  }

  .Home-section {
    flex-direction: column;
  }

  .section {

    margin-left: 10px;
    margin-right: 10px;
  }

  .row {
    flex-direction: column;
  }

  .p_pic {
    height: 200px;
    padding: 30px;
  }

  .nav-items {
    margin-right: 10px;
  }

  .nav-items h4 {
    margin: 10px;
  }

  .text-p h2 {
    font-size: 2.3rem;
    margin: 0;
    text-align: left;
  }

  .text-p h5 {
    font-size: 1.5rem;
    margin: 0;

  }
}

a {
  color: inherit;
  text-decoration: inherit;
}

ul {
  margin: 0;
  padding: 10px;
  overflow: hidden;
}

li {
  float: left;
  margin-right: 1rem;
  margin-left: 1rem;
  font-size: 1.2rem;
}

.about {
  font-size: 1.2rem;
}

.about-email {
  font-size: 1.2rem;
  margin: 0;
}

.animated-div {
  display: flex;
  font-family: 'Courier New', Courier, monospace;
  color: white;
  margin: 0;
  opacity: 0.5;
}

.animated-div p {
  margin: 0;
}

.contact {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.contact label {
  color: #ffffff;
  text-align: left;
  display: block;
}

.contact h2 {
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 10px;
}

.contact input,
.contact textarea{
  width: 100%;
  padding: 12px 15px;
  margin: 10px 0;
  border: 3px solid rgb(54, 53, 53);
  box-sizing: border-box;
  display: block;
  border-radius: 7px;
  font-size: 1rem;
  resize: none
}

.contact button {
  background: rgb(54, 53, 53);
  color: #c2c2c2;
  border: 0;
  padding: 8px 90px;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
}
.skills{
  font-family:'Courier New', Courier, monospace;
  color: white;
  margin: 0;
  opacity: 0.5;

}
.pt2 {
  padding-top: 25px;
}